@include font("sfns-display-regular", HelveticaNeueDeskInterface-Regular);
@include font("SFUIDisplay-Medium", SFUIDisplay-Medium);
@include font("SFUIDisplay-Semibold", SFUIDisplay-Semibold);
@include font("SFUIText-Regular", SFUIText-Regular);
@include font("SFUIText-Medium", SFUIText-Medium);
@include font("SourceSansPro-Regular", SourceSansPro-Regular);
@include font("SourceSansPro-Semibold", SourceSansPro-Semibold);

@mixin f($name, $font-size: null, $line-height: null, $letter-spacing: null) {
  $font-family: null;
  @if $name == "sfr" {
    $font-family: "sfns-display-regular"; }
  @if $name == "sdm" {
    $font-family: "SFUIDisplay-Medium"; }
  @if $name == "sdsb" {
    $font-family: "SFUIDisplay-Semibold"; }
  @if $name == "str" {
    $font-family: "SFUIText-Regular"; }
  @if $name == "stm" {
    $font-family: "SFUIText-Medium"; }
  @if $name == "ssr" {
    $font-family: "SourceSansPro-Regular"; }
  @if $name == "sssb" {
    $font-family: "SourceSansPro-Semibold"; }
  font-family: $font-family, Helvetica Neue, Helvetica, Arial, sans-serif;
  @if $font-family != null {
    font-weight: normal; }
  @if $font-size != null {
    font-size: $font-size * 1px; }
  @if $line-height != null {
    line-height: $line-height/$font-size; }
  @if $letter-spacing != null {
    letter-spacing: ($letter-spacing / 1000) * 1em; } }
