.nav-toggle {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  width: 23px;
  height: 19px;
  display: none;
  padding: 0;
  background-color: transparent;
}

@media only screen and (max-width: 900px) {
  .nav-toggle {
    display: block;
  }
}

.nav-toggle span {
  display: block;
  width: 100%;
  height: 2px;
  border-radius: 6px;
  transition: all 0.2s ease;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.8);
}

.nav-toggle .top {
  top: 0;
  transform-origin: left center;
}

.nav-toggle .middle {
  top: 8px;
  transform-origin: left center;
}

.nav-toggle .bottom {
  top: 16px;
  transform-origin: left center;
}

.nav-toggle.is-open .top {
  transform: rotate(45deg);
  top: 0;
}

.nav-toggle.is-open .middle {
  width: 0;
  opacity: 0;
}

.nav-toggle.is-open .bottom {
  transform: rotate(-45deg);
  top: 16px;
}

