.ether {
  padding: 131px 0 154px 0;
}

.ether .l-col_l {
  width: 63.24786%;
}

.ether .l-col_r {
  width: 36.75214%;
}

.ether__text .h5 {
  margin-bottom: 7px;
}

.ether__text .h3 {
  margin-bottom: 13px;
}

.ether__text p {
  font-size: 17px;
  letter-spacing: 0.2px;
}

.ether__list {
  padding-bottom: 8px;
  border-bottom: 2px solid rgba(151, 151, 151, 0.2);
  margin: 35px 0;
}

.ether__list .item {
  margin-bottom: 27px;
  position: relative;
  padding-left: 50px;
}

.ether__list .item__icon {
  display: block;
  position: absolute;
  top: 1px;
  left: 0;
  width: 38px;
  height: 35px;
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: contain;
}

.ether__list .item__text .h4 {
  margin-bottom: 10px;
}

.ether__list .item__text p {
  font-size: 15px;
}

.ether__more {
  font-size: 16px;
  color: #2733ff;
  font-weight: 500;
  letter-spacing: 0.5px;
  text-decoration: none;
}

.ether__more span, .ether__more svg {
  display: inline-block;
  vertical-align: middle;
}

.ether__more .icon-arrow {
  width: 11px;
  height: 8px;
  stroke: #2733ff;
  margin-left: 8px;
}

.partners {
  padding: 106px 0 237px;
  max-height: 472px;
  margin-bottom: 78px;
}

.partners .l {
  max-width: 1110px;
}

.partners .h3 {
  font-size: 22px;
  letter-spacing: 2px;
  color: #fff;
  margin-bottom: 53px;
}

.partners .companies {
  padding: 0 33px;
  margin: 0 auto 82px;
}

.partners .companies li {
  opacity: .8;
}

.i-title-lg {
  font-family: "SourceSansPro-Semibold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 30px;
  line-height: 1.4;
  letter-spacing: 0.002em;
  text-transform: uppercase;
}

.i-title-md {
  font-family: "SourceSansPro-Semibold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 24px;
  line-height: 1.375;
  letter-spacing: 0.00219em;
  text-transform: uppercase;
}

.i-title-sm {
  font-family: "SFUIText-Medium", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.1875;
  letter-spacing: 0.0005em;
  text-transform: uppercase;
}

.i-sub-title {
  font-family: "SourceSansPro-Semibold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 2.35714;
  letter-spacing: 0.00219em;
  text-transform: uppercase;
}
